import React, { useState } from 'react';
import {
    Button,
    TextInput,
    Title,
    Text,
    Container,
    LoadingOverlay,
    Paper,
    useMantineTheme,
    Space,
    Grid,
} from '@mantine/core';
import { generateWordSearch } from '../utils/wordSearch';
import { fetchWordsFromPrompt } from '../utils/fetchWords';
import jsPDF from 'jspdf';

const WordSearch = () => {
    const [prompt, setPrompt] = useState('');
    const [grid, setGrid] = useState([]);
    const [loading, setLoading] = useState(false);
    const [words, setWords] = useState([]);

    const theme = useMantineTheme();

    const handleGenerate = async () => {
        setLoading(true);
        try {
            const wordArray = await fetchWordsFromPrompt(prompt);
            if (wordArray.length === 0) {
                alert('No words found for this prompt!');
                setLoading(false);
                return;
            }

            const cleanedWordArray = wordArray
                .map(word => word.replace(/[^a-zA-Z0-9]/g, '').trim())
                .filter(word => word.length > 0)
                .slice(0, 15);

            if (cleanedWordArray.length === 0) {
                alert('No valid words found after cleaning!');
                setLoading(false);
                return;
            }

            setWords(cleanedWordArray);
            const newGrid = generateWordSearch(cleanedWordArray);
            setGrid(newGrid);
        } catch (error) {
            console.error("Error fetching words:", error);
            alert('Error fetching words. Please try again.');
        } finally {
            setLoading(false);
        }
    };

    const handleDownload = () => {
        const doc = new jsPDF();
        const numRows = grid.length;
        const numCols = grid[0] ? grid[0].length : 0;

        const fixedBoxSize = 150;
        const cellSize = fixedBoxSize / Math.max(numRows, 1);
        const fontSize = Math.floor(cellSize * 0.7);
        const titleFontSize = 18;

        doc.setFontSize(titleFontSize);
        const titleWidth = doc.getTextWidth(prompt);
        doc.text(prompt, (doc.internal.pageSize.getWidth() - titleWidth) / 2, 10);
        doc.setFontSize(fontSize);

        words.forEach((word, index) => {
            doc.text(`${index + 1}. ${word}`, 10, 40 + (index * fontSize));
        });

        const startX = 30;
        const startY = 40 + words.length * fontSize;
        const boxWidth = fixedBoxSize;
        const boxHeight = fixedBoxSize;

        doc.rect(startX, startY, boxWidth, boxHeight);
        grid.forEach((row, rowIndex) => {
            row.forEach((cell, colIndex) => {
                if (cell) {
                    const x = startX + colIndex * cellSize;
                    const y = startY + rowIndex * cellSize;
                    doc.text(cell, x + cellSize / 2, y + cellSize / 1.5, { align: 'center' });
                }
            });
        });

        doc.save('word-search.pdf');
    };

    return (
        <Container style={{ backgroundColor: theme.colors.dark[6], minHeight: '100vh', padding: '2rem' }}>
            <Title order={1} align="center" color="white" mb="lg">Word Search Generator</Title>
            <Text align="center" size="lg" mt="sm" mb="lg" color="white">
                This app uses a large-language-model to generate crossword puzzles based on an input prompt.
            </Text>        
            <Paper padding="lg" shadow="md" radius="md" style={{ backgroundColor: theme.colors.dark[7] }}>
                <LoadingOverlay visible={loading} overlayBlur={2} />
                <TextInput
                    value={prompt}
                    onChange={(e) => setPrompt(e.target.value)}
                    placeholder="Enter a prompt (e.g., animals, fruits)"
                    label="Prompt"
                    mb="md"
                    style={{
                        backgroundColor: theme.colors.dark[5],
                        color: 'white',
                        fontSize: '1.2rem',
                        height: '60px',
                    }}
                    styles={{ input: { color: 'white' } }}
                />
                <Grid gutter="md">
                    <Grid.Col span={6}>
                        <Button onClick={handleGenerate} loading={loading} fullWidth color="blue" size="lg">
                            Generate Word Search
                        </Button>
                    </Grid.Col>
                    {grid.length > 0 && (
                        <Grid.Col span={6}>
                            <Button onClick={handleDownload} fullWidth color="green" size="lg">
                                Download as PDF
                            </Button>
                        </Grid.Col>
                    )}
                </Grid>
            </Paper>
            <Space h="lg" />
        </Container>
    );
};

export default WordSearch;
